import { Component } from "react";
import {
    EmailShareButton,
    FacebookShareButton,
    HatenaShareButton,
    InstapaperShareButton,
    LineShareButton,
    LinkedinShareButton,
    LivejournalShareButton,
    MailruShareButton,
    OKShareButton,
    PinterestShareButton,
    PocketShareButton,
    RedditShareButton,
    TelegramShareButton,
    TumblrShareButton,
    TwitterShareButton,
    ViberShareButton,
    VKShareButton,
    WhatsappShareButton,
    WorkplaceShareButton
  } from "react-share";
  import { FaTwitterSquare, FaFacebookSquare, FaWhatsappSquare } from "react-icons/fa";
import Card from "./Card";
import ReactMarkdown from 'react-markdown'

export default class Layout extends Component{
    constructor(props){
        super(props)
        this.getBlogBody=this.getBlogBody.bind(this)
    }
    getBlogBody(){
        console.log(
             fetch('/api/v2/pages/?type=blog.BlogPage&fields=body',
        {
            method: "GET",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },

        })
        .then(response => response.json())
        .then(data => {
            console.log(data['items'][0]['body'])
            return data['items'][0]['body']})
        )
    }
    render(){
        return(
            <section className="grid-container">
                  <div className="about" >
                        Follow us on Twitter
                        <FaTwitterSquare/>
                        <hr/>
                        Share:
                        <FaTwitterSquare/>
                        <FacebookShareButton url="google.com">
                            <FaFacebookSquare/>
                        </FacebookShareButton>
                        <FaWhatsappSquare/>
                   </div>
                    <div className="contentLayout">
                    <ReactMarkdown>
                        {this.getBlogBody()}
                    </ReactMarkdown>
                    </div>
                    <div className="related" >
                        <h1>Related</h1>
                        <hr/>
                        <div className="band">
                            <Card/>
                        </div>
                    </div>
            </section>
            
        )
    }
}